import StudyRoadImg from "../../assets/image/Future/StudyRoadImg.svg";
import WorkRoadImg from "../../assets/image/Future/WorkRoadImg.svg";
import OtherRoadImg from "../../assets/image/Future/OtherRoadImg.svg";
import InfoThemeOneIcon from "../../assets/icon/InfoThemeOneIcon.svg";
import "./PageTwo.scss";
import SourceTooltip from "../elements/SourceTooltip";

const element = [
  { image: StudyRoadImg, title: "" },
  { image: WorkRoadImg, title: "" },
  { image: OtherRoadImg, title: "" },
];

const workingStatusData = [
  {
    id: "a",
    name: "",
    percentage: 60,
  },
  {
    id: "b",
    name: "",
    percentage: 30,
  },
  {
    id: "c",
    name: "",
    percentage: 10,
  },
];

const tooltipText = ` : 109111\n200(70%)\n200(70%)\n200(70%)\n200(70%)\n200(70%)\n200(70%)\n200(70%)\n200(70%)`;

// ChartElement: Contains image/percentage/title
/*
  Parameters:
    data: object
    index: string
*/
function ChartElement({ data, index, element }) {
  return (
    <div className="future-page-2__chart__element" key={"future-page-2__chart__element" + index}>
      <img src={element.image} className="future-page-2__chart__element__image" alt={data.name} />
      <div className="future-page-2__chart__element__info">
        {/* Declare different classes by index */}
        <div className={"future-page-2__chart__element__info__percentage--" + index}>{`${data.percentage}%`}</div>
        <div className="future-page-2__chart__element__info__title">{element.title}</div>
      </div>
    </div>
  );
}

function PageTwo({ data }) {
  return (
    <div className="future-page-2">
      <div className="future-page-2__title">
        <SourceTooltip icon={InfoThemeOneIcon} text={tooltipText} />
        <span>
          <strong>  </strong> <br />
           :
        </span>
      </div>
      <div className="future-page-2__chart">
        {data.map((workingStatusInfo, index) => (
          <ChartElement data={workingStatusInfo} index={(index + 1).toString()} element={element[index]} key={workingStatusInfo.id} />
        ))}
      </div>
    </div>
  );
}

export default PageTwo;
